.statistics{
    margin-top: 50px;
    width: 300px;
    margin-left: auto;
    margin-right: auto;
    padding: 50px;
    background-color: gray;
}

.title{
    text-transform: uppercase;
    text-align: center;
    padding-top: 20px;
    
    padding-bottom: 20px;
    color: black;
    background-color: yellowgreen;
    border-radius: 6px;
    box-shadow: 0 10px 10px 5px rgba(0, 0, 0, 0.5);
   
}

.statList{
    margin-top: 20px;
    display: flex;
    flex-wrap: wrap;
    /* background-color: white; */
    
    box-shadow: 0 10px 10px 5px rgba(0, 0, 0, 0.5);
}

.item{
    display: flex;
    flex-wrap: wrap;
    /* gap: 6px; */
    width: calc(100%/5);
    height: 55px;
    color: black;
    padding: 10px 0px;
}

.item span{
    width: 100%;
    text-align: center;
    align-self: center;

}

.label{
    font-size: 16px;
}

.percentage{
    font-size: 20px;
}